.bar__container {
    width: 40px;
    text-align: center;
}

.bar {
    border-radius: 4px;
    width: 80%;
    margin: auto;
    height: 100%;
}

.bar__text {
    margin-top: 10px;
    font-size: 12px;
}

.bar--cyan {
    background-color: hsl(186, 34%, 60%);
}

.bar--soft-red {
    background-color: hsl(10, 79%, 65%);
}

.bar__tooltip {
    background-color: black;
    text-align: center;
    border-radius: 2px;
    padding: 1px 0px;
    margin-bottom: 5px;
    width: 38px;
}

.bar__tooltip p {
    color: white;
    font-size: 10px;
}

@media only screen and (min-width: 768px){
    .bar__container { 
        width: 85px;
    }

    .bar__tooltip { 
        width: 78px;

    }

    .bar__tooltip p { 
        font-size: 15px;
    }

    .bar__text {
        font-size: 20px;
    }
}